#preloader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  overflow: visible;
  background: #fbfbfb;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: all;
}

.loader {
  pointer-events: all;
  width: 100px;
  height: 100px;
  border: 8px solid rgba(255, 107, 8, 0.6);
  border-radius: 50%;
  border-left-color: rgb(255, 107, 8);
  animation: loader 1.3s linear infinite;
}

@keyframes loader {
  100% {
    transform: rotate(360deg);
  }
}

.visible {
  visibility: visible;
  opacity: 1;
  transition: opacity 300ms linear;
}

.hidden {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 300ms, opacity 300ms linear;
}
